<template>
  <div class="page1">
     <div class="header-section">
        <div class="continer">
          <img id="headImg" :src="headPortrait">
            <h1>李凯凯<span>^_^</span></h1>
             <div class="information col-md-6 study-grid">
                <h4>基本信息</h4>
                <table class="col-md-6 study-grid">
                <tr>
                  <td>出生年月:</td>
                  <td>1994年03月</td>
                </tr>
                <tr>
                  <td>政治面貌:</td>
                  <td>党员</td>
                </tr>
                 <tr>
                  <td>工作经验:</td>
                  <td>1.5年</td>
                </tr>
                </table>
                <table class="col-md-6 study-grid">
                <tr>
                  <td>民族:</td>
                  <td>汉族</td>
                </tr>
                <tr>
                  <td>专业:</td>
                  <td>软件技术</td>
                </tr>
                 <tr>
                  <td>联系邮箱:</td>
                  <td>likaikaiwh@163.com</td>
                </tr>
                </table>
              </div>
              <a href="#contact" class="scroll top"><span class="glyphicon glyphicon-triangle-bottom" aria-hidden="true"></span></a>
        </div>
      </div>
  </div>
</template>

<script>
export default {
        name: 'page1',
        data () {
          return {
            headPortrait:require('../assets/images/p1.png')
          }
        },
        methods:{
          toggle:function(int){
            if(  int%2 == 1){
              this.headPortrait = require('../assets/images/p2.png')
            }else{
              this.headPortrait = require('../assets/images/p1.png')
            }
          },
          animate:function(){
            let _this = this;
            setTimeout(function(){
            let headImg = document.getElementById("headImg"),i =0,r =360,int = 0;
                setInterval(function(){
                  var trany = headImg.style.transform;
                  trany = parseInt(trany.split("(")[1]);  
                  if(trany == r ){
                    r+=360;
                    int++;
                    _this.toggle(int);
                  }
                    i+=0.12;
                   headImg.style.transform="rotate("+i+"deg)";
                },10)
            },1000)
          }
        },
       beforeMount () { // 生命周期 created,获取数据
      this.animate()
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.page1 .information h4{
   margin: 15px 0;
}
.page1 .information{
  margin:0 auto;
   overflow: hidden; 
   float: none;
}
.page1 table{

}
.page1 table tr td:nth-child(2){
  color:#00cfbd;
  width: 50%;
}
.page1 table tr td:nth-child(1){
  width: 50%;
}
#headImg{
    border-radius: 50%;
    transition: all .8s
}

#headImg:hover {
    box-shadow: 0 0 10px red;
    opacity: .8
}
</style>



