<template>
<div class="page6">
   <div class="shuffling">
       <div class="fouce fl">
              <div class="focus">
                <ul class="showimg" >
                          <li v-for="(item,index) in shufflingData" v-if='shufflingId==index' v-on:mouseover='showPreNext' v-on:mouseout='hiddenPreNext'>
                              <a target="_blank"  class="img">
                              <img :alt="item.title" :src="item.url" /></a>
                              <h3><a target="_blank" :title="item.title">{{item.title}}</a></h3>
                          </li> 
                </ul>
                 <div class='bullet-pagination'>
                    <a class="bullet" v-bind:class="{'active': shufflingId==0}" v-on:click='bulletFun(0)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==1}" v-on:click='bulletFun(1)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==2}" v-on:click='bulletFun(2)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==3}" v-on:click='bulletFun(3)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==4}" v-on:click='bulletFun(4)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==5}" v-on:click='bulletFun(5)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==6}" v-on:click='bulletFun(6)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==7}" v-on:click='bulletFun(7)'></a>
                    <a class="bullet" v-bind:class="{'active': shufflingId==8}" v-on:click='bulletFun(8)'></a>
                </div>
                <div v-show='PreNext' class="preNext pre" v-on:mouseover='showPreNext' v-on:mouseout='hiddenPreNext' v-on:click='preFun'></div>
                <div v-show='PreNext' class="preNext next" v-on:mouseover='showPreNext' v-on:mouseout='hiddenPreNext' v-on:click='nextFun'></div>
            </div>
        </div> 
    </div>
</div>

</template>

<script>
export default {
     created() {
       this.animate()
     },
     methods: {
       bulletFun:function(int){
         this.shufflingId=int;
       },
       showPreNext:function(){
        this.PreNext=true;
       },
       hiddenPreNext:function(){
        this.PreNext=false;
       },
       preFun:function(){
         var _this=this;
         if(_this.shufflingId>0&&_this.shufflingId<_this.shufflingData.length){
         _this.shufflingId=parseInt(_this.shufflingId)-1;
         }
       },
       nextFun:function(){
         var _this=this;
         if(_this.shufflingId>=0&&_this.shufflingId<_this.shufflingData.length-1){
         _this.shufflingId=parseInt(_this.shufflingId)+1;
         }
       },
       animate(){
        let _this=this;
        setInterval(function() {
         if(_this.shufflingId>=0 && _this.shufflingId<_this.shufflingData.length - 1){
         _this.shufflingId = parseInt(_this.shufflingId)+1;
         }
         else{
         _this.shufflingId=0;
         }
       }, 4000)
       },
     },
     data() {
           return {
                   shufflingData:[{
                   title:'"襄阳政务标准化平台"主页一',
                   url:require('../assets/index01.png')
                 },
                 {
                   title:'"襄阳政务标准化平台"主页二',
                   url:require('../assets/index02.png')
                 },{
                   title:'"襄阳政务标准化平台"主页三',
                   url:require('../assets/index03.png')
                 },{
                   title:'"综合价值提升分析系统"一',
                   url:require('../assets/jzts01.png')
                 },{
                   title:'"综合价值提升分析系统"二',
                   url:require('../assets/jzts02.png')
                 },{
                   title:'"综合价值提升分析系统"三',
                   url:require('../assets/jzts03.png')
                 },{
                   title:'"综合价值提升分析系统"四',
                   url:require('../assets/jzts04.png')
                 },{
                   title:'"综合价值提升分析系统"五',
                   url:require('../assets/jzts05.png')
                 },{
                   title:'"综合价值提升分析系统"六',
                   url:require('../assets/jzts06.png')
                 }],
                   shufflingId:0,
                   PreNext:false,
                 }
           }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shuffling{
  overflow: hidden;
}
  .fouce {
 position: relative;
 left:0;
 overflow: hidden;
 height: 570px;
 width:100%;
}
.fl {
 float: left;
}
.focus{
 overflow: hidden;
}
.fouce ul {
 position: absolute;
}
.fouce ul li {
  margin:0 auto;

}
.fouce ul li a.img {
 display: block;
}
.showimg{
 width:100%;
 left:-0px;
}
.showimg img {
 margin: 0 auto;
 height: 530px;
}
.fouce .bullet-pagination {
 position: absolute;
 bottom: 50px;
}
.fouce ul li h3 {
 height: 40px;
 line-height: 40px;
 background-color: #ededed;
 text-align: center;
 font-size: 25px;
 width: 100%;
}
.bullet-pagination {
 width: 100%;
 text-align: center;
 padding-top: 16px;
 clear: both;
 overflow: hidden;
}
.bullet {
 display: inline-block;
 background: #fff;
 width: 12px;
 height: 12px;
 border-radius: 6px;
 -webkit-border-radius: 6px;
 margin-right: 5px;
 opacity: 0.8;
 -webkit-transition: opacity 0.8s linear;
 -moz-transition: opacity 0.8s linear;
 -ms-transition: opacity 0.8s linear;
 -o-transition: opacity 0.8s linear;
 transition: opacity 0.8s linear;
}
.bullet.active {
 background: #007cdb;
 opacity: 1;
 cursor: pointer;
}
.preNext {
 display: block;
 width: 25px;
 height: 50px;
 position: absolute;
 top: 200px;
 cursor: pointer;
}
.pre {
 background: url('../assets/left.png') no-repeat ;
 left:100px;
}
.next {
 background: url('../assets/left.png') no-repeat -25px;
 right: 100px;
}
* {
 padding: 0;
 margin: 0;
 list-style: none;
}
a{
 text-decoration: none;
}
</style>



