<template>
 <div> <!-- class="social-icons" -->
            <h3>PROJECT</h3>
            <div class="container">
                <div class="col-md-6 face">
                   <div class="bg5s">
                    <span class="left" @click='reduce'></span>
                       <img :src="src01[nowIndex].src" class="ddy">
                    <span class="right" @click='add'></span>
                    </div>
                   <p>"咚咚邮"项目</p>
                </div>
                <div class="col-md-6 face">
                     <div class="bg5s">
                      <span class="left" @click='toggle'></span>
                         <img :src="src02[nowIndex2].src" class="ddy">
                        <span class="right" @click='toggle'></span>
                     </div>
                      <p>"中国邮政投票"项目</p>
                </div>
               
                <div class="clearfix"> </div>
            </div>
 </div>
</template>
<script>
     export default {
        data(){
            return{
                    nowIndex:0,
                    nowIndex2:0,
                    src01:[{src:require('../assets/ddy/ddy01.png')},
                            {src:require('../assets/ddy/ddy02.png')},
                            {src:require('../assets/ddy/ddy03.png')},
                            {src:require('../assets/ddy/ddy04.png')},
                            {src:require('../assets/ddy/ddy05.png')},
                            {src:require('../assets/ddy/ddy06.png')},
                            {src:require('../assets/ddy/ddy07.png')},
                            {src:require('../assets/ddy/ddy08.png')},
                            {src:require('../assets/ddy/ddy09.png')},
                            {src:require('../assets/ddy/ddy10.png')}
                           ],
                    src02:[{src:require('../assets/yz/yz01.png')},
                        {src:require('../assets/yz/yz02.png')}]
            }
        },
         methods:{
          toggle(){
                if(this.nowIndex2 == 0){
                     this.nowIndex2 =1
                }else{
                    this.nowIndex2 =0
                }
             
          },
           add(){
            if(this.nowIndex < this.src01.length-1){
                this.nowIndex++;
            }else{
                this.nowIndex =0;
            }
          },
          reduce(){
            if(this.nowIndex > 0){
                this.nowIndex--;
            }else{
                this.nowIndex =this.src01.length;
            }
          }
        }
     }   

</script>
<style scoped>
    .bg5s{
    width: 377px;
    height: 723px;
    margin: 0 auto;
    background-image: url(../assets/5sbg.png);
    background-size: 100% 100%;
    position: relative;

    }
    .ddy{
    position: absolute;
    top: 101px;
    left: 25px;
    }
    .container .face p {
    font-size: 2em;
    color: #B4BEB9;
    font-weight: 500;
    margin: 0 9px;
}
.left,.right{
    display: inline-block;
    width: 25px;
    height: 45px;
    position: absolute;
    top: 350px;
    background:url(../assets/left.png);
}
.left{
    left:-50px;
}
.right{
    right: -50px;
    background-position: -26px 0;
}
.left:hover,.right:hover{
    cursor:pointer;
}
</style>


